[ng-cloak] .splash-screen {
  display: block !important; }

.splash-screen {
  display: none;
  background: whitesmoke;
  position: fixed;
  z-index: 999999999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  font-family: Helvetica, monospace; }
  .splash-screen.show-always {
    display: block !important; }
  .splash-screen .loader {
    height: 20px;
    width: 250px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto; }
    .splash-screen .loader--dot {
      animation-name: loader;
      animation-timing-function: ease-in-out;
      animation-duration: 3s;
      animation-iteration-count: infinite;
      height: 20px;
      width: 20px;
      border-radius: 100%;
      background-color: black;
      position: absolute;
      border: none; }
      .splash-screen .loader--dot:first-child {
        background-color: #8cc759;
        animation-delay: 0.5s; }
      .splash-screen .loader--dot:nth-child(2) {
        background-color: #8c6daf;
        animation-delay: 0.4s; }
      .splash-screen .loader--dot:nth-child(3) {
        background-color: #ef5d74;
        animation-delay: 0.3s; }
      .splash-screen .loader--dot:nth-child(4) {
        background-color: #f9a74b;
        animation-delay: 0.2s; }
      .splash-screen .loader--dot:nth-child(5) {
        background-color: #60beeb;
        animation-delay: 0.1s; }
      .splash-screen .loader--dot:nth-child(6) {
        background-color: #fbef5a;
        animation-delay: 0s; }
    .splash-screen .loader--text {
      position: absolute;
      top: 200%;
      left: 0;
      right: 0;
      width: 4rem;
      margin: auto; }
      .splash-screen .loader--text:after {
        content: "Loading";
        font-weight: bold;
        animation-name: loading-text;
        animation-duration: 3s;
        animation-iteration-count: infinite; }

@keyframes loader {
  15% {
    transform: translateX(0); }
  45% {
    transform: translateX(230px); }
  65% {
    transform: translateX(230px); }
  95% {
    transform: translateX(0); } }

@keyframes loading-text {
  0% {
    content: "Loading"; }
  25% {
    content: "Loading."; }
  50% {
    content: "Loading.."; }
  75% {
    content: "Loading..."; } }
